import { createContext, useCallback, useContext, useEffect, useState } from "react";
import { useCookies } from "react-cookie";

const authContext = createContext();

export const useAuth = () => useContext(authContext);

export const AuthProvider = ({ children }) =>
    <authContext.Provider value={useAuthProvider()} children={children} />

function useAuthProvider() {

    const [cookie, setCookie, removeCookie] = useCookies(['auth']);
    const [authenticated, setAuthenticated] = useState(false);
    const [token, setToken] = useState(cookie.token);

    const login = (phone, password) =>
        fetch(`http://77.122.219.11:8080/login?phone=${phone}&password=${password}`)
            .then(value => value.text())
            .then(value => setToken(value));

    const registration = (phone, nickname, password) =>
        fetch(`http://77.122.219.11:8080/registration?phone=${phone}&nickname=${nickname}&password=${password}`)
            .then(value => value.text())
            .then(value => setToken(value));

    const edit = (nickname) =>
        fetch(`http://77.122.219.11:8080/edit?token=${token}&nickname=${nickname}`)
            .then(value => value.text());

    const internalValidation = useCallback(() =>
        fetch(`http://77.122.219.11:8080/validation?token=${token}`)
            .then(value => value.json())
            .then(value => {
                setCookie('token', token);
                setAuthenticated(true);
                return value;
            }).catch(error => {
                removeCookie('token');
                setAuthenticated(false);
                return error;
            }),
        [token, setCookie, removeCookie]);

    const validation = useCallback(() => {
        return (token !== undefined && token.length > 10) ? internalValidation() : Promise.all([{}]);
    }, [token, internalValidation]);

    const logout = () => {
        removeCookie('token');
        setAuthenticated(false);
    }

    useEffect(() => validation, [token, validation]);

    return {
        edit: edit,
        token: token,
        logout: logout,
        auth: authenticated,
        reg: registration,
        val: validation,
        log: login,
    }
}
