import { t } from 'i18next';
import { useEffect, useState } from 'react';
import { useAuth } from '../hook/useAuth';
import usePopup from '../hook/usePopup';
import Popup from '../Popup';
import styles from './Form.module.scss';

const Registration = () => {
    const openLogin = usePopup().login[2];
    const registration = useAuth().reg;
    const isAuthenticated = useAuth().auth;
    const [active, setActive] = usePopup().registration;

    const [phone, setPhone] = useState('');
    const [repeat, setRepeat] = useState('');
    const [nickname, setNickname] = useState('');
    const [password, setPassword] = useState('');

    useEffect(() => setActive(isAuthenticated ? false : active), [isAuthenticated, active, setActive]);

    function onContClick(event) {
        event.preventDefault();
        registration(phone, nickname, password);
    }

    function onBackClick(event) {
        event.preventDefault();
        openLogin();
    }

    return (
        <Popup active={active} setActive={setActive}>
            <form className={styles.element}>
                <label htmlFor='phoneID' children={t('phone') + ': '} />
                <input id='phoneID' value={phone} type='tel' placeholder='+(380) __ ___ ____' onChange={e => { setPhone(e.target.value) }} />

                <label htmlFor='nicknameID' children={t('nickname') + ': '} />
                <input id='nicknameID' value={nickname} type='text' placeholder='Nickname' onChange={e => { setNickname(e.target.value) }} />

                <label htmlFor='passwordID' children={t('password') + ': '} />
                <input id='passwordID' value={password} type='new-password' placeholder='Password' onChange={e => { setPassword(e.target.value) }} />

                <label htmlFor='repeatID' children={t('passwordRepeat') + ': '} />
                <input id='repeatID' value={repeat} type='new-password' placeholder='Password repeat' onChange={e => { setRepeat(e.target.value) }} />

                <div className='flex justify-evenly'>
                    <button onClick={onBackClick} children={t('back')} />
                    <button onClick={onContClick} children={t('continue')} />
                </div>

            </form>
        </Popup>
    );
}

export default Registration;