import { useAuth } from "./hook/useAuth";
import { useQueue } from "./hook/useQueue";
import QueueForm from "./QueueForm";
import QueueItem from "./QueueItem";

const QueueList = () => {
    const authenticated = useAuth().auth;
    const queues = useQueue().queues;

    return (
        <div className="bg-gray-200 bg-opacity-60 rounded-lg w-4/6 mx-auto my-10 flex flex-col">
            {authenticated&& <QueueForm />}
            {queues.map((value, index) => <QueueItem key={value.created} queue={value} />)}
        </div>
    );
}

export default QueueList;