import { createContext, useContext, useEffect, useState } from "react";
import { useAuth } from "./useAuth";
import usePopup from "./usePopup";

const queueContext = createContext();

export const useQueue = () => useContext(queueContext);

export const QueueProvider = ({ children }) =>
    <queueContext.Provider value={useQueueProvider()} children={children} />


const useQueueProvider = () => {
    const token = useAuth().token;
    const openQueue = usePopup().queue[2];
    const [queues, setQueues] = useState([]);
    const [queue, setQueue] = useState({});

    const update = () =>
        fetch("http://77.122.219.11:8080/props/list")
            .then(res => res.json())
            .then(res => setQueues(res));

    const open = (value) => {
        setQueue(value);
        openQueue();
    }

    const edit = (status) =>
        fetch(`http://77.122.219.11:8080/props/edit?token=${token}&proposal=${queue.id}&status=${status}`)
            .then(value => update());

    const post = (title, description) =>
        fetch(`http://77.122.219.11:8080/props/create?token=${token}&title=${title}&description=${description}`)
            .then(value => update());

    useEffect(() => { update() }, []);

    return {
        update: update,
        queues: queues,
        queue: queue,
        edit: edit,
        post: post,
        open: open,
    };
}