import { useQueue } from "./hook/useQueue";


function getColor(queue) {
    if (queue.status === 'CONSIDERED')
        return 'bg-yellow-600';
    if (queue.status === 'REJECTED')
        return 'bg-red-600';
    if (queue.status === 'APPROVED')
        return 'bg-green-600';
    return 'bg-gray-600'
}

const QueueItem = ({ queue }) => {
    const openQueue = useQueue().open;

    function onClick(event) {
        event.preventDefault();
        openQueue(queue);
    }

    return (
        <div className={getColor(queue) + " bg-opacity-40 rounded-lg border border-black border-opacity-20 m-2 flex flex-col"} onClick={onClick}>
            <div className="border-b border-black border-opacity-20 text-center p-1">
                <h1>{queue.title}</h1>
            </div>

            <div className="p-3">
                <div className="break-words text-justify indent-5">
                    {queue.description}
                </div>
            </div>

            <div className="border-t border-black border-opacity-20 flex justify-around pl-3">
                <p>{queue.account.nickname}</p>
                <p>+{queue.account.phone}</p>
            </div>
        </div>
    );
}

export default QueueItem;