import { t } from "i18next";
import { useState } from "react";
import { useQueue } from "./hook/useQueue";

const QueueForm = () => {
    const post = useQueue().post;
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');
    return (
        <div className="bg-opacity-40 rounded-lg border border-black border-opacity-20 m-2 flex flex-col">
            <div className="flex justify-between p-1">
                <label htmlFor="titleID" className="w-1/6 ml-1" children={t('title')} />
                <input
                    id="titleID"
                    type='text'
                    className="rounded-lg px-2 bg-sky-200 bg-opacity-60 border border-black border-opacity-60 w-5/6"
                    onChange={e => setTitle(e.target.value)}
                    value={title}
                />
            </div>
            <textarea
                className="rounded-lg p-2 bg-sky-200 bg-opacity-60 border border-black border-opacity-60 mx-1 mb-1 h-44"
                onChange={e => setDescription(e.target.value)}
                value={description}
            />
            <div className="flex justify-end">
                <div
                    className="mb-1 mr-1 p-1 px-2 bg-green-600 bg-opacity-70 rounded-lg border border-black border-opacity-60 hover:cursor-pointer hover:bg-opacity-100"
                    children={t('add')}
                    onClick={e=>{
                        post(title, description).then(()=>{
                            setDescription('')
                            setTitle('');
                        });
                    }}
                />
            </div>
        </div>
    );

}

export default QueueForm;