
const Popup = ({ active, setActive, children }) => {
    return active ? (
        <div className="fixed w-full h-full bg-black bg-opacity-60 top-0 left-0 flex items-center justify-center" onClick={() => setActive(false)}>
            <div className="bg-gray-300 rounded-lg border border-opacity-60" onClick={e => e.stopPropagation()}>
                {children}
            </div>
        </div>
    ) : '';
}


export default Popup;