import { t } from 'i18next';
import { useEffect, useState } from 'react';
import { useAuth } from '../hook/useAuth';
import usePopup from '../hook/usePopup';
import Popup from "../Popup";
import styles from './Form.module.scss';

const Login = () => {
    const login = useAuth().log;
    const isAuthenticated = useAuth().auth;
    const openRegistration = usePopup().registration[2];
    const [active, setActive] = usePopup().login;
    const [password, setPassword] = useState('');
    const [phone, setPhone] = useState('');

    useEffect(()=> setActive(isAuthenticated ? false : active), [isAuthenticated, active, setActive]);

    function onSignClick(event) {
        event.preventDefault();
        login(phone, password);
    }

    function onRegClick(event) {
        event.preventDefault();
        openRegistration();
    }

    return (
        <Popup active={active} setActive={setActive}>
            <form className={styles.element}>
                <label htmlFor='phoneID' children={t('phone') + ': '} />
                <input id='phoneID' value={phone} type='tel' placeholder='+(380) __ ___ ____' onChange={e => setPhone(e.target.value)} />

                <label htmlFor='passwordID' children={t('password') + ': '} />
                <input id='passwordID' value={password} type='current-password' placeholder='Password' onChange={e => setPassword(e.target.value)} />

                <div className='flex justify-evenly'>
                    <button onClick={onSignClick} children={t('sign-in')} />
                    <button onClick={onRegClick} children={t('registration')} />
                </div>
            </form>
        </Popup>
    );
}

export default Login;