import { useAuth } from "./hook/useAuth";
import usePopup from "./hook/usePopup";


const AuthIcon = () => {
    const isAuthenticated = useAuth().auth;
    const openProfile = usePopup().profile[2];
    const openLogin = usePopup().login[2];

    const pathImage = isAuthenticated ? 'http:/image/home.svg' : 'http:/image/plus.svg';

    return (
        <img className="fixed right-5 bottom-5 hover:cursor-pointer " src={pathImage} alt='' width='50px' height='50px' onClick={e => {
            isAuthenticated ? openProfile() : openLogin();
        }} />
    )
}


export default AuthIcon;