import { createContext, useContext, useState } from "react";

const popupContext = createContext();

export const PopupProvider = ({ children }) => {
    const value = {
        queue: useState(false),
        login: useState(false),
        profile: useState(false),
        registration: useState(false),
    };

    Object.entries(value).forEach(([currenKey, currenValue]) => {
        value[currenKey] = [...currenValue, () =>
            Object.entries(value).forEach(([contextKey, contextValue]) =>
                contextValue[1](currenKey === contextKey)
            )
        ]
    });

    return <popupContext.Provider value={value} children={children} />;
}

export const PopupConsumer = ({ children }) =>
    <popupContext.Consumer>
        {children}
    </popupContext.Consumer>;


const usePopup = () => useContext(popupContext);

export default usePopup;