import { t } from 'i18next';
import { useEffect, useState } from 'react';
import DatePicker from 'react-date-picker';
import { useAuth } from '../hook/useAuth';
import usePopup from '../hook/usePopup';
import { useQueue } from '../hook/useQueue';
import Popup from "../Popup";

function getColor(status) {
    if (status === 'CONSIDERED')
        return 'bg-yellow-600';
    if (status === 'REJECTED')
        return 'bg-red-600';
    if (status === 'APPROVED')
        return 'bg-green-600';
    return 'bg-gray-600'
}


const Queue = () => {
    const edit = useQueue().edit;
    const queue = useQueue().queue;
    const validation = useAuth().val;
    const isAuthenticated = useAuth().auth;
    const [active, setActive] = usePopup().queue;
    const [status, setStatus] = useState(queue?.status);
    const [admin, setAdmin] = useState(false);

    useEffect(() => { setStatus(queue?.status) }, [queue]);
    useEffect(() => { validation().then(value => setAdmin(value?.role === 'ADMIN')) }, [validation]);

    const QueueButton = ({ status, children }) =>
        <div
            className={'px-1 mx-1 rounded-lg border border-opacity-40 border-black hover:cursor-pointer bg-opacity-60 hover:bg-opacity-100 ' + getColor(status)}
            onClick={() => { setStatus(status); edit(status); }}
            children={children}
        />


    return (
        <Popup active={active} setActive={setActive}>
            <div className='flex flex-col md:flex-row'>
                <div className={'flex flex-col items-stretch sm:border-b md:border-b-0 md:border-r border-black justify-between w-96 sm:rounded-t-lg md:rounded-r-none md:rounded-l-lg bg-opacity-60 ' + getColor(status)}>
                    <div className='border-b border-black text-center py-2'>{queue?.title}</div>
                    <div className='break-words text-justify indent-5 p-2'>{queue?.description}</div>
                    <div className='border-t border-black flex justify-center py-2'>
                        <pre>{t('publicationDate')}: </pre>
                        <div><DatePicker value={new Date(queue?.created)} onChange={() => { }} /></div>
                    </div>
                </div>
                <div className='flex flex-col mx-2'>
                    <div className='flex mt-2'>
                        <pre>{t('nickname')}: </pre>
                        <div>{queue?.account?.nickname}</div>
                    </div>
                    <div className='flex mt-2'>
                        <pre>{t('userPhone')}: </pre>
                        <div>+{queue?.account?.phone}</div>
                    </div>
                    <div className='flex mt-2'>
                        <pre>{t('dateRegistration')}: </pre>
                        <div><DatePicker value={new Date(queue?.account?.created)} onChange={() => { }} /></div>
                    </div>
                    {
                        (admin && isAuthenticated) ? (
                            <div className='flex  my-2'>
                                <QueueButton edit={edit} status='APPROVED' children={t('approved')} />
                                <QueueButton edit={edit} status='CONSIDERED' children={t('processed')} />
                                <QueueButton edit={edit} status='REJECTED' children={t('rejected')} />
                            </div>
                        ) : ''
                    }
                </div>
            </div>
        </Popup>
    )
}

export default Queue;