import { AuthProvider } from "./useAuth";
import { PopupProvider } from "./usePopup";
import { QueueProvider } from "./useQueue";

const GenericHook = ({ children }) =>
    <PopupProvider>
        <AuthProvider>
            <QueueProvider>
                {children}
            </QueueProvider>
        </AuthProvider>
    </PopupProvider>

export default GenericHook;