import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "../..";
import { useAuth } from "../hook/useAuth";
import usePopup from "../hook/usePopup";
import Popup from "../Popup";
import styles from './Form.module.scss';


const ChangeLanguage = () => {
    return (
        <select onClick={e => i18n.changeLanguage(e.target.value)}>
            {
                i18n.options.supportedLngs.map((value, index) => (value !== 'cimode') ? (
                    <option key={index} value={value} selected={value === i18n.language}>{value}</option>
                ) : '')
            }
        </select>
    );
}

const Profile = () => {
    const edit = useAuth().edit;
    const logout = useAuth().logout;
    const validation = useAuth().val;
    const isAuthenticated = useAuth().auth;
    const [active, setActive] = usePopup().profile;
    const [t] = useTranslation();
    const [nickname, setNickname] = useState('')
    const [phone, setPhone] = useState('');

    useEffect(() => {
        validation().then(value => {
            setNickname(value?.nickname);
            setPhone(value?.phone);
        });
    }, [validation]);

    useEffect(() => setActive(isAuthenticated ? active : false), [isAuthenticated, active, setActive]);

    function onSave(event) {
        event.preventDefault();
        edit(nickname);
    }

    function onExit(event) {
        event.preventDefault();
        logout();
    }

    return (
        <Popup active={active} setActive={setActive}>
            <form className={styles.element}>
                <ChangeLanguage />
                <input type='text' value={nickname} onChange={e => setNickname(e.target.value)} />
                <input type='text' value={phone} unselectable />
                <div className='flex justify-evenly'>
                    <button children={t('save')} onClick={onSave} />
                    <button children={t('exit')} onClick={onExit} />
                </div>
            </form>
        </Popup>
    );
}

export default Profile;